.skills {
	width: 100%;
	height: 80%;

	// .skills_list
	&_list {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
		max-width: 800px;
	}

	// .skills__title
	&__title {
		font-size: 1.6rem;
		line-height: 1.8rem / 1.6rem * 100%;
		margin-bottom: 60px;
	}
}
